@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
*,
*::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


:root {
  /* primary */
  --Lime-Green: hsl(163, 72%, 41%);
  --Bright-Red: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram: hsl(37, 97%, 70%);
  --Instagram2: hsl(329, 70%, 58%);
  --YouTube: hsl(348, 97%, 39%);
  --Toggle: linear gradient hsl(210, 78%, 56%) to hsl(146, 68%, 55%);
  --Toggle-light: hsla(230, 22%, 74%, 0.2);
    



    --White-BG: hsl(0, 0%, 100%);
    --Background-card: hsl(225, 100%, 98%);
    --Background-body: hsl(227, 47%, 96%);
    --Paragraph-color: hsl(228, 12%, 44%);
    --Heading: hsl(230, 17%, 14%);
}


[data-theme = "dark"] {

  --Background-body: hsl(230, 17%, 14%);
    --Very-Dark-Blue-Top-BG-Pattern: hsl(232, 19%, 15%);
    --Background-card: hsl(228, 28%, 20%);
    --Paragraph-color: hsla(228, 34%, 66%, 1);
    --Heading: hsl(0, 0%, 100%);

}
.App{
  background-color: var(--Background-body);
  min-height: 100vh;
}
#root{
  min-height:100vh ;
}


body {
  font-family: "Roboto", sans-serif;
  background-color: var(--Background-body);

  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: var(--Very-Dark-Blue-Text);
  overflow-x: hidden;
  min-height: 100vh;
}

h1 {
  margin-bottom: 0.2rem;
  color: var(--Heading);
}

h2 {
  margin-top: 1rem;
}

p {
  color: var(--Paragraph-color);
}

header {
  text-align: start;
  font-weight: 700;
}

hr {
  border: 0;
  height: 1px;
  background-color: black;
  opacity: 0.7;
  margin-top: 1rem;
}

.dark {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  margin-top: 1rem;
}

input[type="range"] {
  -webkit-appearance: none;
  margin-right: 15px;
  width: 2rem;
  height: 1rem;
  background: grey;
  border-radius: 50px;
}

.container {
  display: grid;
  column-gap: 1rem;
  justify-content: center;
  min-width: 100vw;
 
}

.subscribers {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  min-width: 90vw;
}

.card {
  display: flex;
  flex-direction: column;
  background-color: var(--Background-card);
  gap: 1rem;
  min-width: 90vw;
}

.link {
  display: flex;
  text-decoration: none;
  color: #000;
  gap: 0.5rem;
  margin-bottom: 1rem;
  color: var(--Very-Dark-Blue-Text);
}

.facebook {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: solid 3px var(--Facebook);
  border-radius: 5px;
  background-color: var(--Toggle-light);
  padding: 1rem;
}

.Twitter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: solid 3px var(--Twitter);
  border-radius: 5px;
  background-color: var(--Toggle-light);
  padding: 1rem;
}

.Instagram {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: solid 3px var(--Instagram);
  border-radius: 5px;
  background-color: var(--Toggle-light);
  padding: 1rem;
}

.Youtube {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-top: solid 3px var(--YouTube);
  border-radius: 5px;
  background-color: var(--Toggle-light);
  padding: 1rem;
}
.num {
  font-size: 3rem;
  font-weight: 700;
  color: unset;
}

.followers {
  text-transform: uppercase;
  letter-spacing: 5px;
  color: var(--Dark-Grayish-Blue-Text);
}

.today {
  display: flex;
  align-items: center;
  margin-top: 1.5rem;
  color: var(--Lime-Green);
  font-weight: 700;
}

.Youtube .today {
  color: var(--Bright-Red);
}

.reactions {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-top: 1rem;
  background-color: var(--Toggle-light);
  padding: 1rem 1.5rem;
  border-radius: 5px;
}

.reactions .link {
  display: flex;
  text-decoration: dashed;
  justify-content: space-between;
}

.reactions :first-child {
  grid-column: span 2;
}

.reactions .num {
  font-size: 2rem;
  font-weight: 700;
  color: unset;
}

.reactions .today {
  column-span: 2;
  justify-content: end;
}

/* dark-theme */

@media screen and (min-width: 650px) {
  .card {
    min-width: none;
  }

  .subscribers {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 1rem;
  }

  .overview {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 1rem;
  }
}
